/* ================================== */
/*          GENERAL SETTINGS          */
/* ================================== */
html {
  box-sizing: border-box;
  font-size: 16px; }

*,
*::after,
*::before {
  box-sizing: inherit; }

body {
  display: grid;
  min-height: 100vh;
  color: #E6E7EB;
  font-family: "Open Sans", Arial, Helvetica, sans-serif;
  background-color: #292A36;
  background-image: linear-gradient(155deg, #545567, #292A36); }

ul {
  list-style: none; }

ul li {
  display: inline-block; }

ol li {
  display: block;
  list-style: none; }

/* ------------- LAYOUT ------------- */
/* ================================== */
/*             MAIN HEADER            */
/* ================================== */
.header {
  grid-area: header;
  display: flex;
  justify-content: center;
  align-items: center; }
  .header__heading {
    margin: 0;
    text-align: center;
    font-weight: 500;
    font-size: 50px;
    line-height: 50px;
    letter-spacing: 10px;
    color: #ffffff;
    text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.25); }

/* ================================== */
/*               FOOTER               */
/* ================================== */
.footer {
  grid-area: footer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 45px;
  text-align: center;
  font-size: 12px;
  background-color: #7BAE53; }
  .footer__link:link, .footer__link:visited, .footer__link:active, .footer__link:focus {
    color: currentColor;
    text-decoration: none; }
  .footer__link:hover {
    text-decoration: underline; }

/* ================================== */
/*              MAIN MAIN             */
/* ================================== */
.main {
  grid-area: content;
  display: flex;
  justify-content: center;
  margin-bottom: 50px; }

/* ------------- EDITOR ------------- */
.editor {
  display: flex;
  flex-direction: column;
  width: 100%; }
  .editor__header {
    margin: 0;
    font-size: 32px;
    font-weight: 600;
    color: #7BAE53; }
    .editor__header .material-icons--edit {
      margin-left: 10px;
      font-size: 32px; }
  .editor__tools {
    display: flex;
    justify-content: space-between;
    margin: 0; }
  .editor__box {
    display: flex; }
    .editor__box p {
      margin: 0 2px 0 0; }
  .editor__input {
    width: 100%;
    height: 48px;
    padding-left: 21px;
    line-height: 48px;
    font-size: 14px;
    background-color: #7BAE53;
    border-radius: 5px 5px 0 0; }
  .editor__textarea {
    display: flex;
    flex-direction: column; }
  .editor__textarea-edit {
    width: 100%;
    height: 352px;
    padding: 10px;
    border-radius: 0 0 5px 5px; }
  .editor__textarea-elements {
    display: flex;
    justify-content: space-between;
    margin-top: -12px; }
  .editor__time-publish {
    line-height: 36px;
    font-size: 12px; }

/* -------------- SIDEBAR -------------- */
.sidebar {
  padding: 40px 0 0 24px; }

/* ================================== */
/*             MAIN SIDEBAR           */
/* ================================== */
.sidebar {
  grid-area: sidebar;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-height: 442px; }

/* ================================== */
/*         MAIN GRID SETTINGS         */
/* ================================== */
/* ----------- DESKTOP LARGE---------- */
@media screen and (min-width: 1281px) {
  .wrapper {
    display: grid;
    grid-template-columns: 1fr 865px 256px 1fr;
    grid-template-rows: [header-start] 145px [header-end content-start] auto [content-end footer-start] 45px [footer-end];
    grid-template-areas: "header header header header" "... content sidebar ..." "footer footer footer footer"; } }

@media screen and (max-width: 1280px) {
  .wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: [header-start] 145px [header-end content-start] auto [content-end footer-start] 45px [footer-end];
    grid-template-areas: "header header header header" "content content content sidebar" "footer footer footer footer"; }
  main {
    margin-left: 20px; } }

@media screen and (max-width: 1024px) {
  .wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: [header-start] 100px [header-end content-start] auto [content-end sidebar-start] auto [sidebar-end footer-start] 45px [footer-end];
    grid-template-areas: "header header header header" "content content content content" "sidebar sidebar sidebar sidebar" "footer footer footer footer"; }
  main {
    margin: 0 20px 0 20px; }
  .header__heading {
    font-size: 40px; }
  .sidebar {
    flex-direction: row;
    margin-right: 20px; }
    .sidebar__theme-switchers {
      width: 30%; }
    .sidebar__cleanning-up {
      display: flex;
      flex-direction: column; } }

@media screen and (max-width: 640px) {
  .wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: [header-start] 100px [header-end content-start] auto [content-end sidebar-start] auto [sidebar-end footer-start] 45px [footer-end];
    grid-template-areas: "header header header header" "content content content content" "sidebar sidebar sidebar sidebar" "footer footer footer footer"; }
  main {
    margin: 0 5px 0 5px; }
  .header__heading {
    font-size: 32px; }
  .editor__header {
    text-align: center;
    font-size: 30px;
    margin-bottom: 20px; }
  .editor .material-icons--edit {
    font-size: 30px; }
  .editor__box {
    display: flex;
    justify-content: space-between;
    align-items: space-between; }
  .editor__tools {
    flex-direction: column;
    justify-items: center; }
  .sidebar {
    flex-direction: column-reverse; }
    .sidebar__theme-switchers {
      width: 100%; }
    .sidebar__cleanning-up {
      display: flex;
      flex-direction: column;
      margin-bottom: 20px; } }

