/* ------------ COMPONENTS ----------- */
.alert-description {
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 48px;
  margin: -48px 0 0 0;
  text-align: center;
  line-height: 48px;
  text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.35); }
  .alert-description--success {
    background-color: #7BAE53;
    animation: slideDownUp 5s ease-in-out forwards; }
  .alert-description--error {
    background-color: #ce1d43;
    animation: slideDownUp 5s ease-in-out forwards; }

/* ================================== */
/*      BUTTONS CLEANNIG MEMORY       */
/* ================================== */
.sidebar__btn {
  position: relative;
  min-width: 190px;
  height: 36px;
  margin-right: 2px;
  background-color: #7BAE53;
  border: none;
  border-radius: 5px;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer; }

.sidebar__btn:first-child {
  margin-bottom: 5px; }

.material-icons--delete, .material-icons--refresh {
  position: absolute;
  top: 6px;
  left: 2px;
  font-size: 26px; }

/* ================================== */
/*       BUTTONS FORMATING TEXT       */
/* ================================== */
.editor__btn {
  position: relative;
  min-width: 40px;
  border: none;
  background: none;
  color: #7BAE53;
  font-size: 30px;
  text-align: center;
  cursor: pointer; }
