/* ================================== */
/*          THEMES SWITCHERS          */
/* ================================== */
.sidebar {
  /* --------- MAIN SWITCHER --------- */
  /* --------- OLIVE SWITCHER --------- */
  /* --------- BLUE SWITCHER ---------- */
  /* --------- YELLOW SWITCHER --------- */ }
  .sidebar__theme-switchers {
    display: flex;
    flex-direction: column;
    font-weight: 300; }
  .sidebar__label {
    cursor: pointer;
    padding-right: 5px;
    position: relative;
    display: block;
    font-size: 14px;
    padding: 10px 0; }
    .sidebar__label--blue {
      padding-top: 0; }
  .sidebar__input[type="radio"] {
    position: absolute;
    visibility: hidden !important; }
  .sidebar__input[type="radio"] + .sidebar__label::before, .sidebar__input[type="radio"] + .sidebar__label::after {
    content: '';
    position: absolute;
    top: 50%;
    margin-top: -12px;
    box-sizing: border-box; }
  .sidebar__input[type="radio"] + .sidebar__label::before {
    width: 48px;
    height: 24px;
    right: 30px;
    background: #E6E7EB;
    border-radius: 15px; }
  .sidebar__input[type="radio"] + .sidebar__label::after {
    width: 18px;
    height: 18px;
    margin-top: -9px;
    right: 57px;
    background: #E6E7EB;
    border-radius: 50%;
    transition: all 200ms ease-out; }
  .sidebar__input[type="radio"] + .sidebar__label--olive::before {
    background: #7BAE53; }
  .sidebar__input[type="radio"] + .sidebar__label--blue::before {
    background: #2D9CDB; }
  .sidebar__input[type="radio"] + .sidebar__label--yellow::before {
    background: #F2C94C; }
  .sidebar__input--olive-js[type="radio"]:checked + .sidebar__label--olive-js::after {
    right: 33px;
    background: #545567; }
  .sidebar__input--blue-js[type="radio"]:checked + .sidebar__label--blue-js::after {
    right: 33px;
    background: #545567; }
  .sidebar__input--yellow-js[type="radio"]:checked + .sidebar__label--yellow-js::after {
    right: 33px;
    background: #545567; }
