.alert-description {
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 48px;
    margin: -48px 0 0 0;
    text-align: center;
    line-height: 48px;
    text-shadow: 2px 2px 3px rgba(0, 0, 0, .35);

    &--success {
    background-color: $olive;
    animation: slideDownUp 5s ease-in-out forwards;
    }

    &--error {
        background-color: $red;
        animation: slideDownUp 5s ease-in-out forwards;
    }
}
