/* ================================== */
/*             MAIN HEADER            */
/* ================================== */

.header {
    grid-area: header;
    display: flex;
    justify-content: center;
    align-items: center;
    
    &__heading {
        margin: 0;
        text-align: center;
        font-weight: 500;
        font-size: 50px;
        line-height: 50px;
        letter-spacing: 10px;
        color: $white;
        text-shadow: 1px 1px 5px rgba(0,0,0, .25); 
    }
}